@media print {
  /* your print styles */
  .raleigh-campaign.Campaign.CampaignType--floating div,
  .site-header .header-right-column,
  #page .site-main .post-nav-wrapper,
  #page .site-main .social-share-wrapper,
  aside.sidebar .location-callout-box-basic,
  aside.sidebar .featured-posts-block,
  aside.sidebar .categories-block,
  #page .site-main .call-to-action,
  footer.primary-footer {
    display: none;
  }
}

main.site-main .section-title-with-line {
  margin-left: -20px;
}

@media (max-width: 767.98px) {
  main.site-main .section-title-with-line {
    margin-left: 0px;
    margin-bottom: 25px;
  }
}

main.site-main .section-title-with-line h1:before, main.site-main .section-title-with-line h1:after {
  background-color: #177E89;
}

@media (max-width: 767.98px) {
  main.site-main .pagination-wrap {
    border-top: 0px;
    margin-top: 0.625rem;
    margin-bottom: 1.875rem;
  }
}

@media (max-width: 767.98px) {
  main.site-main .entry-container {
    padding-bottom: 0;
  }
}

main.site-main .entry-container .post-date p {
  font-style: normal;
}

main.site-main .entry-container .post-date a {
  font-weight: normal;
}

main.site-main #blog article.post {
  margin-bottom: 38px;
  padding: 20px 20px 30px;
  border-radius: 15px;
  background: #ffffff;
  box-shadow: 0px 2px 25px rgba(0, 0, 0, 0.09);
}

@media (max-width: 767.98px) {
  main.site-main #blog article.post {
    border-bottom: 0px;
  }
}

main.site-main #blog article.post:last-child {
  margin-bottom: 0;
  border-bottom: 0;
}

main.site-main #blog article.post .entry-container h2 {
  padding-bottom: 0;
}

@media (max-width: 767.98px) {
  main.site-main #blog article.post {
    padding-bottom: 0.625rem;
    margin-bottom: 2.5rem;
    border-bottom: 0;
  }
  main.site-main #blog article.post .alpha.entry-title {
    margin-top: 1rem;
    margin-bottom: .5rem;
  }
}

aside.blog-sidebar .location-callout-box-basic {
  margin-bottom: 2rem;
}

aside.blog-sidebar .featured-posts-block, aside.blog-sidebar .categories-block {
  padding-bottom: 2rem;
}

aside.blog-sidebar .featured-posts-block .title, aside.blog-sidebar .categories-block .title {
  position: relative;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding-left: 40px;
  margin-left: -40px;
}

@media (max-width: 767.98px) {
  aside.blog-sidebar .featured-posts-block .title, aside.blog-sidebar .categories-block .title {
    padding-left: 0;
    margin-left: 0;
  }
}

aside.blog-sidebar .featured-posts-block .title h4, aside.blog-sidebar .categories-block .title h4 {
  font-size: 22px;
  line-height: 24px;
  font-weight: bold;
}

aside.blog-sidebar .featured-posts-block .title h4:before, aside.blog-sidebar .categories-block .title h4:before {
  content: "";
  position: absolute;
  top: 10px;
  margin: auto;
  left: 5px;
  display: block;
  height: 43px;
  width: 3px;
  transform: rotate(23deg);
  background-color: #FFC857;
}

@media (max-width: 575.98px) {
  aside.blog-sidebar .featured-posts-block .title h4:before, aside.blog-sidebar .categories-block .title h4:before {
    height: 35px;
    left: 10px;
  }
}

@media (max-width: 767.98px) {
  aside.blog-sidebar .featured-posts-block .title h4:before, aside.blog-sidebar .categories-block .title h4:before {
    display: none;
  }
}

aside.blog-sidebar .featured-posts-block .title h4:after, aside.blog-sidebar .categories-block .title h4:after {
  content: "";
  position: absolute;
  top: 10px;
  margin: auto;
  left: 15px;
  display: block;
  height: 43px;
  width: 3px;
  transform: rotate(23deg);
  background-color: #084C61;
}

@media (max-width: 575.98px) {
  aside.blog-sidebar .featured-posts-block .title h4:after, aside.blog-sidebar .categories-block .title h4:after {
    height: 35px;
    left: 18px;
  }
}

@media (max-width: 767.98px) {
  aside.blog-sidebar .featured-posts-block .title h4:after, aside.blog-sidebar .categories-block .title h4:after {
    display: none;
  }
}

aside.blog-sidebar .featured-posts-block .featured-posts-list, aside.blog-sidebar .featured-posts-block .categories-list, aside.blog-sidebar .categories-block .featured-posts-list, aside.blog-sidebar .categories-block .categories-list {
  padding-left: 0;
  list-style-type: none;
  line-height: 1.5;
  font-size: 1rem;
}

aside.blog-sidebar .featured-posts-block .featured-posts-list a, aside.blog-sidebar .featured-posts-block .categories-list a, aside.blog-sidebar .categories-block .featured-posts-list a, aside.blog-sidebar .categories-block .categories-list a {
  font-weight: 300;
}

aside.blog-sidebar .featured-posts-block .featured-posts-list ul.children, aside.blog-sidebar .featured-posts-block .categories-list ul.children, aside.blog-sidebar .categories-block .featured-posts-list ul.children, aside.blog-sidebar .categories-block .categories-list ul.children {
  line-height: 1.5;
  list-style-type: none;
}
